import React from 'react';
import './App.css';
import ToggleButton from '@material-ui/lab/ToggleButton';
import ToggleButtonGroup from '@material-ui/lab/ToggleButtonGroup';
import Tags from './tags';
import Recommend from './recommend';


export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      tab: "tags"
    };
  }
  handleTab(event) {
    const target = event.target;
    this.setState({
      tab: event.value
    });
  }

  render() {
    let tagsTab = <Tags/>
    let recommendTab = <Recommend/>
    
    return (
      <div className="App">
        <img src={"/images/LogoFullB.png"} className="App-logo" alt="logo" />
        <ToggleButtonGroup
          value = {this.state.tab}
          exclusive
          onChange={(event) => this.handleTab(event)}
          aria-label="tabs"
        >
          <ToggleButton value="tags">
              Tags
          </ToggleButton>
          <ToggleButton value="recommend">
              Recommend
          </ToggleButton>
        </ToggleButtonGroup>
        { (this.state.tab === "tags") ? tagsTab : recommendTab }
      </div>
    );
  }
}



